import { useEffect, useState } from "react";
import GameBoard from "../components/GameBoard";

const icons = ["üçé","üçå","üçá","üçì","üçí","üçç","ü•ù","ü•ë"];

export default function Home() {
  const [cards, setCards] = useState([]);
  const [selected, setSelected] = useState([]);
  const [moves, setMoves] = useState(0);
  const [time, setTime] = useState(0);
  const [timerId, setTimerId] = useState(null);
  const [gameWon, setGameWon] = useState(false);
  const [playerName, setPlayerName] = useState("");

  useEffect(() => startGame(), []);

  useEffect(() => {
    if (cards.length > 0 && cards.every((c) => c.matched)) {
      clearInterval(timerId);
      setGameWon(true);
    }
  }, [cards]);

  const startGame = () => {
    const shuffled = [...icons, ...icons]
      .map((icon) => ({ icon }))
      .sort(() => Math.random() - 0.5)
      .map((card, index) => ({
        id: index,
        icon: card.icon,
        flipped: false,
        matched: false,
      }));

    setCards(shuffled);
    setMoves(0);
    setTime(0);
    setGameWon(false);
    setPlayerName("");

    const id = setInterval(() => setTime((t) => t + 1), 1000);
    setTimerId(id);
  };

  const handleCardClick = (card) => {
    if (selected.length === 2) return;

    setCards((p) =>
      p.map((c) => (c.id === card.id ? { ...c, flipped: true } : c))
    );

    if (selected.length === 0) setSelected([card]);
    else {
      setSelected(([first]) => {
        setMoves((m) => m + 1);
        if (first.icon === card.icon) {
          setCards((p) =>
            p.map((c) =>
              c.icon === card.icon ? { ...c, matched: true } : c
            )
          );
          return [];
        } else {
          setTimeout(() => {
            setCards((p) =>
              p.map((c) =>
                c.id === first.id || c.id === card.id
                  ? { ...c, flipped: false }
                  : c
              )
            );
          }, 800);
          return [];
        }
      });
    }
  };

  const saveScore = async () => {
    if (!playerName) return alert("Enter name!");

    await fetch("/api/leaderboard/add", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ name: playerName, moves, time }),
    });

    startGame();
  };

  return (
    <div className="min-h-screen bg-slate-900 text-white flex flex-col items-center p-6">
      <h1 className="text-3xl font-bold mb-4">Memory Card Game</h1>

      <div className="flex gap-6 mb-4">
        <p className="text-xl">‚è± Time: {time}s</p>
        <p className="text-xl">üéØ Moves: {moves}</p>
        <button
          onClick={startGame}
          className="bg-blue-600 px-4 py-2 rounded-lg"
        >
          Restart üîÅ
        </button>
      </div>

      <GameBoard cards={cards} onCardClick={handleCardClick} />

      {gameWon && (
        <div className="mt-6 p-4 bg-green-600 rounded-lg text-center">
          <h2 className="text-xl font-bold">üéâ YOU WIN!</h2>
          <p>Enter your name to save score</p>
          <input
            className="mt-2 p-2 rounded text-black"
            placeholder="Your Name"
            value={playerName}
            onChange={(e) => setPlayerName(e.target.value)}
          />
          <button
            className="ml-2 bg-black px-4 py-2 rounded"
            onClick={saveScore}
          >
            Save ‚úÖ
          </button>
        </div>
      )}
    </div>
  );
}
